import React, { useState } from 'react';
import {
  Button,
  CardBody,
  FormGroup,
  Input,
  Label,
  NavItem,
  NavLink,
  TabPane,
} from 'reactstrap';
import classNames from 'classnames';
import { connect } from 'react-redux';

import { Colxx } from '../../../../../components/common/CustomBootstrap';
import IntlMessages from '../../../../../helpers/IntlMessages';
import Rating from '../../../../../components/common/Rating';
import useRequest from '../../../../../hooks/use-request';
import NotificationManager from '../../../../../components/common/react-notifications/NotificationManager';

const FeedbackNavItem = ({ activeTab, setActiveTab }) => (
  <NavItem>
    <NavLink
      className={classNames({
        active: activeTab === 'feedback',
        'nav-link': true,
      })}
      onClick={() => setActiveTab('feedback')}
      to="#"
      location={{}}
    >
      <IntlMessages id="menu.feedback" />
    </NavLink>
  </NavItem>
);

const FeedbackTabPane = ({ profileData, hackathon }) => {
  const [values, setValues] = useState({
    user_id: profileData.id,
    hackathon_id: hackathon.id,
    comment: '',
    star: 0,
  });

  const handleChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.value });
  };

  const [doRequest, requestLoading] = useRequest({
    url: '/addFeedback',
    method: 'POST',
    data: values,
    nextSuccess: () => {
      NotificationManager.success(
        'Feedback submitted successfully!',
        'Feedback Success',
        3000,
        null,
        null,
        ''
      );
      // refresh(accessToken, id);
    },
    nextError: (error) => {
      NotificationManager.error(error, 'Feedback Error', 3000, null, null, '');
    },
  });

  const handleSubmit = () => {
    doRequest();
  };

  return (
    <TabPane tabId="feedback">
      <Colxx xxs="12" className="mt-4">
        <CardBody>
          <FormGroup className="mb-4">
            <Label>
              <IntlMessages id="menu.feedback" />
            </Label>
            <Input
              id="comment"
              type="textarea"
              rows="6"
              value={values.comment}
              onChange={handleChange}
            />
          </FormGroup>

          <FormGroup>
            <Label>
              <IntlMessages id="challenges.rating" />
            </Label>
            <Rating
              total={5}
              rating={values.star}
              onRate={({ rating }) => {
                setValues({ ...values, star: rating });
              }}
            />
          </FormGroup>

          <FormGroup className="text-center">
            <Button
              id="submit-basics"
              type="submit"
              color="primary"
              className={`btn-shadow btn-multiple-state ${
                requestLoading ? 'show-spinner' : ''
              }`}
              onClick={handleSubmit}
            >
              <span className="spinner d-inline-block">
                <span className="bounce1" />
                <span className="bounce2" />
                <span className="bounce3" />
              </span>
              <span className="label">
                <IntlMessages id="forms.submit" />
              </span>
            </Button>
          </FormGroup>
        </CardBody>
      </Colxx>
    </TabPane>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { profileData } = authUser;

  return { profileData };
};

const ConnectFeedBackTabPane = connect(mapStateToProps, {})(FeedbackTabPane);

export { FeedbackNavItem, ConnectFeedBackTabPane as FeedbackTabPane };
