import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Card, CardHeader, Row, Button } from 'reactstrap';

import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';

import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import SingleLightbox from '../../../../components/application/SingleLightbox';

import NavItems from './NavItems';
import TabPanes from './TabPanes';

import HackDetails from './components/HackDetails';
import useRequest from '../../../../hooks/use-request';
import { log, getProfileData } from '../../../../helpers/Utils';
import { adminRoot } from '../../../../constants/defaultValues';
import IntlMessages from '../../../../helpers/IntlMessages';

const Details = ({ match }) => {
  const history = useHistory();
  const { id } = match.params;
  const profileData = getProfileData();
  const userId = profileData.user_id;
  console.log(userId);
  const [activeTab, setActiveTab] = useState('basics');

  const [data, setData] = useState({});
  const [isLoaded, setIsLoaded] = useState(false);

  const [doRequest, requestLoading, requestError] = useRequest({
    url: `/getdetails/${id}`,
    method: 'GET',

    nextSuccess: (responseData) => {
      setData(responseData.getdetails);
      setIsLoaded(true);
    },
  });

  useEffect(() => {
    doRequest();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (requestLoading || !isLoaded) return <div className="loading" />;

  if (requestError) log(requestError);

  let showButton = 0;
  let status = 0;

  if (data.Participant.length) {
    data.Participant.map((user) => {
      if (user.user_id === userId) {
        showButton = 1;
        status = user.status;
      }
      return null;
    });
  }

  console.log(status);

  return (
    <>
      <Row>
        <Colxx xxs="8">
          <Breadcrumb heading="menu.details" match={match} />
        </Colxx>
        <Colxx xxs="4">
          <div className="mx-auto d-flex justify-content-around">
            {showButton === 1 ? (
              <>
                {status === 0 ? (
                  <button
                    type="button"
                    id="pending"
                    className="btn btn-warning"
                  >
                    <IntlMessages id="hackcard.pending-button" />
                  </button>
                ) : (
                  <button
                    type="button"
                    className="btn btn-success"
                  >
                    <IntlMessages id="hackcard.participated-button" />
                  </button>
                )}
              </>
            ) : (
              <Button
                color="secondary"
                className=""
                onClick={() =>
                  history.push(`${adminRoot}/challenges/register/${id}`)
                }
              >
                <IntlMessages id="hackcard.participate-button" />
              </Button>
            )}

            <button
              id="participant-number"
              type="button"
              className="btn btn-outline-secondary btn-sm"
            >
              <IntlMessages id="number.participants" />:{' '}
              {data.Participant.length}
            </button>
          </div>
        </Colxx>
      </Row>

      <Separator className="mb-5" />

      <Row>
        <Colxx xxs="12" className="mb-5">
          <Card>
            <SingleLightbox
              // thumb="/assets/img/social/header.jpg"
              // large="/assets/img/social/header.jpg"
              thumb={data.hackathon.hackathon_cover_img}
              large={data.hackathon.hackathon_cover_img}
              className="social-header card-img"
            />
          </Card>
        </Colxx>

        <Colxx xxs="12" sm="12" className="mb-4 col-left">
          <SingleLightbox
            // thumb="/assets/img/profiles/l-10.jpg"
            // large="/assets/img/profiles/10.jpg"
            thumb={data.hackathon.hackathon_logo}
            large={data.hackathon.hackathon_logo}
            className="img-thumbnail card-img social-profile-img"
          />
          <HackDetails hackInfo={data} />
          
        </Colxx>

        <Colxx xxs="12" className="mb-4 col-right">
          <Card className="mb-4">
            <CardHeader>
              <NavItems
                Partstatus={status}
                activeTab={activeTab}
                setActiveTab={setActiveTab}
              />

              <TabPanes
                Partstatus={status}
                activeTab={activeTab}
                hackInfo={data}
              />
            </CardHeader>
          </Card>
        </Colxx>
      </Row>
    </>
  );
};

export default Details;
