import React from 'react';
import classNames from 'classnames';

import { Nav, NavItem, NavLink } from 'reactstrap';
import IntlMessages from '../../../../helpers/IntlMessages';
import { BasicsNavItem } from './components/basics';
import { LinksNavItem } from './components/links';
// import { BrandsNavItem } from './components/brands';
import { DatesNavItem } from './components/dates';
import { FaqNavItem } from './components/faq';
import { FeedbackNavItem } from './components/feedback';
import { PrizesNavItem } from './components/prizes';
import { SubmissionNavItem } from './components/submission';

const NavItems = ({ Partstatus, activeTab, setActiveTab }) => {
  return (
    <Nav tabs className="card-header-tabs">
      <BasicsNavItem activeTab={activeTab} setActiveTab={setActiveTab} />

      <LinksNavItem activeTab={activeTab} setActiveTab={setActiveTab} />

      {/* <BrandsNavItem activeTab={activeTab} setActiveTab={setActiveTab} /> */}

      <DatesNavItem activeTab={activeTab} setActiveTab={setActiveTab} />

      <PrizesNavItem activeTab={activeTab} setActiveTab={setActiveTab} />

      <NavItem>
        <NavLink
          className={classNames({
            active: activeTab === 'judges',
            'nav-link': true,
          })}
          onClick={() => setActiveTab('judges')}
          to="#"
          location={{}}
        >
          <IntlMessages id="menu.judges" />
        </NavLink>
      </NavItem>

      <NavItem>
        <NavLink
          className={classNames({
            active: activeTab === 'sponsors',
            'nav-link': true,
          })}
          onClick={() => setActiveTab('sponsors')}
          to="#"
          location={{}}
        >
          <IntlMessages id="menu.sponsors" />
        </NavLink>
      </NavItem>

      <FaqNavItem activeTab={activeTab} setActiveTab={setActiveTab} />

      {Partstatus ? <SubmissionNavItem activeTab={activeTab} setActiveTab={setActiveTab} /> : <></> }

      {Partstatus ? <FeedbackNavItem activeTab={activeTab} setActiveTab={setActiveTab} /> : <></> }
    </Nav>
  );
};

export default NavItems;
