import React from 'react';
import classNames from 'classnames';
import { CardBody, NavItem, NavLink, TabPane } from 'reactstrap';
import InputDisplay from '../../../../../containers/application/InputDisplay';
import IntlMessages from '../../../../../helpers/IntlMessages';

export const BasicsNavItem = ({ activeTab, setActiveTab }) => (
  <NavItem>
    <NavLink
      className={classNames({
        active: activeTab === 'basics',
        'nav-link': true,
      })}
      onClick={() => setActiveTab('basics')}
      to="#"
      location={{}}
    >
      <IntlMessages id="menu.details" />
    </NavLink>
  </NavItem>
);

export const BasicsTabPane = ({ hackathon }) => (
  <TabPane tabId="basics">
    <CardBody>
      <InputDisplay
        messageId="challenges.hackathon-name"
        name="hackathon-name"
        value={hackathon.name}
      />

      <InputDisplay
        messageId="challenges.hackathon-tagline"
        name="hackathon-tagline"
        value={hackathon.tagline}
      />

      <InputDisplay
        messageId="challenges.about"
        name="hackathon-about"
        value={hackathon.about}
        intputType="textarea"
      />

      <InputDisplay
        messageId="challenges.max-team-size"
        name="max-team-size"
        value={hackathon.max_team_size}
      />
    </CardBody>
  </TabPane>
);
