import React from 'react';
import { CardBody, NavItem, NavLink, Row, TabPane } from 'reactstrap';
import classNames from 'classnames';
import IntlMessages from '../../../../../helpers/IntlMessages';
import { Colxx } from '../../../../../components/common/CustomBootstrap';
import InputDisplay from '../../../../../containers/application/InputDisplay';

export const LinksNavItem = ({ activeTab, setActiveTab }) => (
  <NavItem>
    <NavLink
      className={classNames({
        active: activeTab === 'links',
        'nav-link': true,
      })}
      onClick={() => setActiveTab('links')}
      to="#"
      location={{}}
    >
      <IntlMessages id="menu.links" />
    </NavLink>
  </NavItem>
);

export const LinksTabPane = ({ hackathon }) => (
  <TabPane tabId="links">
    <Row>
      <Colxx sm="12">
        <CardBody>
          <InputDisplay
            messageId="challenges.hackathon-website"
            name="hackathon-website"
            value={hackathon.hackathon_website}
          />

          <InputDisplay
            messageId="challenges.portfolio-url"
            name="portfolio-url"
            value={hackathon.portfolio_url}
          />

          <InputDisplay
            messageId="challenges.contact-email"
            name="contact-email"
            value={hackathon.contact_email}
          />

          <InputDisplay
            messageId="challenges.link-to-code-of-control"
            name="link-to-code-of-control"
            value={hackathon.link_to_code_of_control}
          />

          <InputDisplay
            messageId="challenges.facebook"
            name="facebook"
            value={hackathon.facebook}
          />

          <InputDisplay
            messageId="challenges.twitter"
            name="twitter"
            value={hackathon.twitter}
          />

          <InputDisplay
            messageId="challenges.instagram"
            name="instagram"
            value={hackathon.instagram}
          />

          <InputDisplay
            messageId="challenges.linkedin"
            name="linkedin"
            value={hackathon.linkedin}
          />
        </CardBody>
      </Colxx>
    </Row>
  </TabPane>
);
