import React from 'react';
import classNames from 'classnames';
import {Card, CardBody,CardTitle, NavItem, NavLink, Row, TabPane } from 'reactstrap';
import { Colxx } from '../../../../../components/common/CustomBootstrap';
import IntlMessages from '../../../../../helpers/IntlMessages';

export const PrizesNavItem = ({ activeTab, setActiveTab }) => {
  return (
    <NavItem>
      <NavLink
        className={classNames({
          active: activeTab === 'prizes',
          'nav-link': true,
        })}
        onClick={() => setActiveTab('prizes')}
        to="#"
        location={{}}
      >
        <IntlMessages id="menu.prizes" />
      </NavLink>
    </NavItem>
  );
};

export const PrizesTabPane = ({ prizes }) => (
  <TabPane tabId="prizes">
    <Row>
      <Colxx sm="12">
        <CardBody>
        {prizes.map((item, idx) => (
              // eslint-disable-next-line react/no-array-index-key
              <Card key={idx} className="mb-1">
                <CardBody>
                  <CardTitle>{item.name}</CardTitle>
                  {item.sponsor_prize === 1 && (
                    <div className="position-absolute card-top-buttons">
                      Sponsor Prize:{' '}
                      <i className="simple-icon-check text-success" />
                    </div>
                  )}
                  {item.description}
                </CardBody>
              </Card>
            ))}
        </CardBody>
      </Colxx>
    </Row>
  </TabPane>
);
