import React from 'react';

import { CardBody, Row, TabContent, TabPane } from 'reactstrap';
import { Colxx } from '../../../../components/common/CustomBootstrap';

// import IntlMessages from '../../../../helpers/IntlMessages';
import { ApplicationTabPane } from './components/application';
import { BasicsTabPane } from './components/basics';
// import { BrandsTabPane } from './components/brands';
import { DatesTabPane } from './components/dates';
import { FaqTabPane } from './components/faq';
import { FeedbackTabPane } from './components/feedback';
import { LinksTabPane } from './components/links';
import { PrizesTabPane } from './components/prizes';
import { SubmissionTabPane } from './components/submission';
import JudgeCard from '../../../../components/cards/JudgeCard';
import SponsorCard from '../../../../components/cards/SponsorCard';
import { getCurrentUser } from '../../../../helpers/Utils';

const TabPanes = ({ Partstatus, activeTab, hackInfo }) => {
  const {id} = getCurrentUser();
  const { hackathon, sponsors, prizes, judges, Faq, application } = hackInfo;
  const applicationData = application || {
    about: [],
    contact: [],
    education: [],
    experience: [],
    online_profiles: [],
  };
  console.log(Partstatus);
  return (
    <TabContent activeTab={activeTab}>
      <BasicsTabPane hackathon={hackathon} />

      <ApplicationTabPane applicationData={applicationData} />

      <LinksTabPane hackathon={hackathon} />
{/* 
      <BrandsTabPane hackathon={hackathon} /> */}

      <DatesTabPane hackathon={hackathon} />

      <PrizesTabPane prizes={prizes} />

      <TabPane tabId="sponsors">
        <Row>
          <Colxx sm="12">
            <CardBody>
              {sponsors.map((item) => (
                <SponsorCard key={item.id} data={item} />
              ))}
            </CardBody>
          </Colxx>
        </Row>
      </TabPane>

      <TabPane tabId="judges">
        <Row>
          <Colxx sm="12">
            <CardBody>
              {judges.map((item) => (
                  <JudgeCard 
                    key={item.id} 
                    data={item} 
                  />
              ))}
            </CardBody>
          </Colxx>
        </Row>
      </TabPane>
      

      <FaqTabPane Faq={Faq} />

      {Partstatus ? <SubmissionTabPane submission={hackInfo.hackathon_submission} user={id}/>:<></>}
      {Partstatus ? <FeedbackTabPane hackathon={hackathon} />:<></>}
    </TabContent>
  );
};

export default TabPanes;
