import React from 'react';
import { Badge, CardBody, Row, TabPane } from 'reactstrap';
import { Colxx } from '../../../../../components/common/CustomBootstrap';
import { hackthonApplication } from '../../../../../constants/defaultValues';
import InputDisplay from '../../../../../containers/application/InputDisplay';
import IntlMessages from '../../../../../helpers/IntlMessages';

export const ApplicationNavItem = () => null;

export const ApplicationTabPane = ({ applicationData }) => {
  const applicationAboutData = hackthonApplication.about.filter(({ value }) =>
    applicationData.about.includes(value)
  );
  const applicationContactData = hackthonApplication.contact.filter(
    ({ value }) => applicationData.contact.includes(value)
  );
  const applicationEducationData = hackthonApplication.education.filter(
    ({ value }) => applicationData.education.includes(value)
  );
  const applicationExpericeData = hackthonApplication.experience.filter(
    ({ value }) => applicationData.experience.includes(value)
  );
  const applicationOnlineProfileData = hackthonApplication.online_profiles.filter(
    ({ value }) => applicationData.online_profiles.includes(value)
  );

  return (
    <TabPane tabId="application">
      <Row>
        <Colxx sm="12">
          <CardBody>
            <div className="mb-4">
              <div className="lead h4">
                <IntlMessages id="challenges.about-title" />
              </div>

              {applicationAboutData.map((item) => (
                <Badge
                  key={item.key}
                  color="outline-primary"
                  pill
                  className="m-2"
                >
                  <IntlMessages id={item.messageId} />
                </Badge>
              ))}
            </div>

            <div className="mb-4">
              <div className="lead h4">
                <IntlMessages id="challenges.education-title" />
              </div>

              {applicationEducationData.map((item) => (
                <Badge
                  key={item.key}
                  color="outline-primary"
                  pill
                  className="m-2"
                >
                  <IntlMessages id={item.messageId} />
                </Badge>
              ))}
            </div>

            <div className="mb-4">
              <div className="lead h4">
                <IntlMessages id="challenges.experience-title" />
              </div>

              {applicationExpericeData.map((item) => (
                <Badge
                  key={item.key}
                  color="outline-primary"
                  pill
                  className="m-2"
                >
                  <IntlMessages id={item.messageId} />
                </Badge>
              ))}
            </div>

            <div className="mb-4">
              <div className="lead h4">
                <IntlMessages id="challenges.online-profiles-title" />
              </div>

              {applicationOnlineProfileData.map((item) => (
                <Badge
                  key={item.key}
                  color="outline-primary"
                  pill
                  className="m-2"
                >
                  <IntlMessages id={item.messageId} />
                </Badge>
              ))}
            </div>

            <div className="mb-4">
              <div className="lead h4">
                <IntlMessages id="challenges.contact-title" />
              </div>

              {applicationContactData.map((item) => (
                <Badge
                  key={item.key}
                  color="outline-primary"
                  pill
                  className="m-2"
                >
                  <IntlMessages id={item.messageId} />
                </Badge>
              ))}
            </div>

            <InputDisplay
              messageId="challenges.custom-questions"
              name="theme-color-code"
              value="This is a Custom Question"
            />
          </CardBody>
        </Colxx>
      </Row>
    </TabPane>
  );
};
