import classNames from 'classnames';
import React from 'react';
import { useHistory, useParams } from 'react-router-dom';
import {
  Button,
  CardBody,
  FormGroup,
  Label,
  NavItem,
  NavLink,
  TabPane,
} from 'reactstrap';
import {
  Colxx,
  Separator,
} from '../../../../../components/common/CustomBootstrap';
import { adminRoot } from '../../../../../constants/defaultValues';
import IntlMessages from '../../../../../helpers/IntlMessages';
import SubmissionCard from '../../../../../components/cards/SubmissionCard';

const SubmissionNavItem = ({ activeTab, setActiveTab }) => {
  return (
    <NavItem>
      <NavLink
        className={classNames({
          active: activeTab === 'submission',
          'nav-link': true,
        })}
        onClick={() => setActiveTab('submission')}
        to="#"
        location={{}}
      >
        <IntlMessages id="menu.submission" />
      </NavLink>
    </NavItem>
  );
};


const SubmissionTabPane = ({submission, user}) => {
 const history = useHistory();
  const { id } = useParams();
  return (
    <TabPane tabId="submission">
      <Colxx xxs="12" className="mt-4">
        <CardBody className="">
          <FormGroup className="mb-4 d-flex justify-content-between">
            <Label className="lead">
              <IntlMessages id="menu.submission" />
            </Label>

            <Button
              onClick={() =>
                history.push(`${adminRoot}/challenges/${id}/submission`)
              }
            >
              Submit New
            </Button>
          </FormGroup>

          <Separator className="my-4" />

          <FormGroup>
            <Label className="h5">Your Submissions </Label>
          </FormGroup>

          {submission.map((item) =>{
            if (item.user_id === user) {
              return (
                  <SubmissionCard
                    key={item.id} 
                    data={item} 
                  />
                );
            }
            return null;
          })}
        </CardBody>
      </Colxx>
    </TabPane>
  );
};

export { SubmissionTabPane, SubmissionNavItem };
