import React from 'react';
import { CardBody, NavItem, NavLink, Row, TabPane } from 'reactstrap';
import classNames from 'classnames';
import IntlMessages from '../../../../../helpers/IntlMessages';
import { Colxx } from '../../../../../components/common/CustomBootstrap';
import InputDisplay from '../../../../../containers/application/InputDisplay';

export const DatesNavItem = ({ activeTab, setActiveTab }) => (
  <NavItem>
    <NavLink
      className={classNames({
        active: activeTab === 'dates',
        'nav-link': true,
      })}
      onClick={() => setActiveTab('dates')}
      to="#"
      location={{}}
    >
      <IntlMessages id="menu.dates" />
    </NavLink>
  </NavItem>
);

export const DatesTabPane = ({ hackathon }) => (
  <TabPane tabId="dates">
    <Row>
      <Colxx sm="12">
        <CardBody>
          <InputDisplay
            messageId="challenges.hackthon-application-begins"
            name="application-start"
            value={hackathon.application_start}
          />

          <InputDisplay
            messageId="challenges.hackthon-application-ends"
            name="application-ends"
            value={hackathon.application_end}
          />

          <InputDisplay
            messageId="challenges.hackthon-hackathon-begins"
            name="hackathon-starts"
            value={hackathon.hackathon_start}
          />

          <InputDisplay
            messageId="challenges.hackthon-hackathon-ends"
            name="hackathon-ends"
            value={hackathon.hackathon_date}
          />

          <InputDisplay
            messageId="challenges.announcement-results"
            name="accouncement-date"
            value={hackathon.results_date}
          />

          {/* <InputDisplay
            messageId="challenges.timezone"
            name="timezone"
            value={hackathon.timezone}
          /> */}
        </CardBody>
      </Colxx>
    </Row>
  </TabPane>
);
