import React, { useState } from 'react';

import { NavItem, NavLink, TabPane } from 'reactstrap';
import classNames from 'classnames';
import IntlMessages from '../../../../../helpers/IntlMessages';
import { Colxx } from '../../../../../components/common/CustomBootstrap';
import FaqCard from '../../../../../components/cards/FaqCard';

export const FaqNavItem = ({ activeTab, setActiveTab }) => (
  <NavItem>
    <NavLink
      className={classNames({
        active: activeTab === 'faq',
        'nav-link': true,
      })}
      onClick={() => setActiveTab('faq')}
      to="#"
      location={{}}
    >
      <IntlMessages id="menu.faq" />
    </NavLink>
  </NavItem>
);
export const FaqTabPane = ({ Faq }) => {
  const [showingIndex, setShowIndex] = useState(0);
  return (
    <TabPane tabId="faq">
      <Colxx xxs="12" className="mt-4">
        {Faq.map((item, index) => (
          <FaqCard
            // eslint-disable-next-line react/no-array-index-key
            key={`faqItem_${index}`}
            item={item}
            index={index}
            showingIndex={showingIndex}
            setShowIndex={setShowIndex}
          />
        ))}
      </Colxx>
    </TabPane>
  );
};
